import { SpreadsType } from '../data/cardSpreads/type';

interface Card {
    number: number;
    isRevers: boolean;
}

export interface CardDescription {
    number: number;
    description: string;
    isRevers: boolean;
}

export function getDescriptionForCard(
    card: Card,
    positionNumber: number,
    spreads: SpreadsType
): CardDescription {
    console.log("card", card?.number)
    console.log("positionNumber", positionNumber)
    const prevCardNumber = card.number - 1;

    const position = spreads.positions.find((p) => p.number === positionNumber);
    if (!position) {
        throw new Error(
            `Position for positionNumber number ${positionNumber} not found`
        );
    }

    const targetCard = position.card.find((c) => c.number === prevCardNumber);

    if (!targetCard) {
        throw new Error(`Card for card number ${card.number} not found`);
    }

    //   
    const cardInfo = position.card.find((c) => c.number === prevCardNumber);
    console.log("cardInfo", cardInfo)
    if (!cardInfo) {
        throw new Error(
            `Card number ${card.number} not found in position ${position.number}`
        );
    }

    //      
    return {
        number: card.number,
        isRevers: card.isRevers,
        description: card.isRevers
            ? cardInfo.reversDescriptions
            : cardInfo.descriptions,
    };
}

export class SeededRandom {
    private seed: number;

    constructor(seed: number) {
        this.seed = seed;
    }

    //   
    private next(): number {
        const a = 1664525;
        const c = 1013904223;
        const m = 2 ** 32;

        this.seed = (a * this.seed + c) % m;
        return this.seed / m;
    }

    public nextBoolean(): boolean {
        return this.next() < 0.5;
    }

    //        
    public nextInRange(min: number, max: number): number {
        return Math.floor(this.next() * (max - min + 1)) + min;
    }
}

export function generateCardDetails(numbers: number[], seed: number): Card[] {
    const rng = new SeededRandom(seed);
    return numbers.map((num) => ({
        number: num,
        isRevers: rng.nextBoolean(), //    
    }));
}

export function generateRandomNumbers(fullName: string): number[] {
    //     seed
    let seed = 0;
    for (let i = 0; i < fullName.length; i++) {
        seed += fullName.charCodeAt(i);
    }

    //     seed
    const currentDate = new Date().toISOString().slice(0, 10); //    
    for (let i = 0; i < currentDate.length; i++) {
        seed += currentDate.charCodeAt(i);
    }

    const rng = new SeededRandom(seed);

    //      1  78
    const randomNumbers: number[] = [];
    for (let i = 0; i < 10; i++) {
        randomNumbers.push(rng.nextInRange(1, 78));
    }

    return randomNumbers;
}

